import './App.css';
import { Route, BrowserRouter } from "react-router-dom";
import Home from './pages/Home'
import Resume from './pages/Resume'

function App() {
  return (
    <BrowserRouter>
      <Route path="/" exact component={Home} />
      <Route path="/resume" component={Resume} />
    </BrowserRouter>
  )
}

export default App;
