import React from "react";
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button'
import '../styles/Home.css';
import resumetakashi from '../assets/resume-takashi.pdf';
import wisc from '../assets/wisc.png';
import saying from '../assets/saying-web.png';
import paper from '../assets/paper-blur.png'
import Navigation from '../components/Navigation';
import Sidebar from "../components/Sidebar";
import Card from 'react-bootstrap/Card';
import Breadcrumb from 'react-bootstrap/Breadcrumb';

const Home = () => {
    return (
        <div id="home">
            <Navigation />
            <Container>
                <Row>
                    <Col><Sidebar/></Col>
                    <Col xs={9}>
                        <div className="resume-container">
                            <h3 id="about"> About Me </h3>
                            <Button
                                variant="success"
                                onClick={(e) => {
                                    e.preventDefault();
                                    window.open(resumetakashi);
                                }}
                            > View Resume   --&gt; </Button>
                        </div>
                        <div className='paragraph'>
                            <p>I am Takashi Matsuzawa, a 2nd year CS master student @ UW-Madison.</p> 
                            <p>Earned my CS bachelor's degree @ UW-Madison from 2017 - 2019.</p> 
                            <p>I am currently looking for full-time/part-time position as a software engineer or full-stack developer.</p>
                        </div>

                        <h3 className="title" id="project">Projects</h3>
                        <div className="current-project">
                        <img src={saying} alt="saying-web"/>
                            <Card>
                                <Card.Header>
                                    <h4>Saying Theatre 2.0</h4>
                                    <p>A fully responsive website designed for Saying Theatre. I was able to:</p>
                                    <p>
                                        1. Simpilify and Migrate the REST database API into Amazon AWS API and Github API; built up an application that directly updated API from Googlesheet. <br/>
                                        2. Accomplish half of the responsive features for better user experiences, including the front page, contact page, and more. <br/>
                                        3. Fix bugs that were left from Saying Theatre 1.0 (e.g. duplicate member cards when loading more).
                                    </p>
                                    <div className="url-group">
                                        <p>Check out the live site here: &nbsp;</p><a href="http://www.sayingtheatre.com" rel="noreferrer" target="_blank">http://www.sayingtheatre.com</a>
                                    </div>
                                    <div className="url-group">
                                        <p>English (in progress): &nbsp;</p><a href="http://www.sayingtheatre.com/en" rel="noreferrer" target="_blank">http://www.sayingtheatre.com/en</a>
                                    </div>
                                </Card.Header>
                            </Card>
                        </div>
                        <div className="current-project">
                            <img src={paper} alt="paper"/>
                            <Card>
                                <Card.Header>
                                    <h4>Insights on AR/VR Applications Creation for Beginners</h4>
                                    <p> ABSTRACT: 
                                        Commercialized AR/VR technologies are increasingly being
                                        utilized by players and consumers. However, for content creators and especially beginners with little prior knowledge in
                                        designing or programming skills, creating AR/VR works can
                                        still be difficult [2, 9]. In this paper, I use ethnographic data to
                                        examine how beginners behave during their learning processes...
                                        
                                    </p>
                                    <h5>More details are in progress.</h5>
                                </Card.Header>
                            </Card>
                        </div>
                        <div className="current-project">
                            <Card>
                                <Card.Body>
                                    <Card.Title> SayingTheatre 1.0 </Card.Title>
                                    <Breadcrumb>
                                        <Breadcrumb.Item rel="noreferrer" target="_blank" href="https://github.com/SayingTheatreClub/">SayingTheatreClub</Breadcrumb.Item>
                                        <Breadcrumb.Item rel="noreferrer" target="_blank" className="repo-name" href="https://github.com/SayingTheatreClub/SayingWebFrontend">SayingWebFrontend</Breadcrumb.Item>
                                    </Breadcrumb>
                                    <p>A React and Django based Website built for Saying Theatre. Most of the features has been migrated into version 2.0. </p>
                                </Card.Body>
                            </Card>
                            <Card>
                                <Card.Body>
                                    <Card.Title> Badger Buddy </Card.Title>
                                    <Breadcrumb>
                                        <Breadcrumb.Item rel="noreferrer" target="_blank" href="https://github.com/tmatsuzawa2">tmatsuzawa2</Breadcrumb.Item>
                                        <Breadcrumb.Item rel="noreferrer" target="_blank" className="repo-name" href="https://github.com/tmatsuzawa2/badger-buddy">Badger-Buddy</Breadcrumb.Item>
                                    </Breadcrumb>
                                    <p>A django based forum for UW-Madison students to share and release negative feelings, or hope to find help.</p>
                                </Card.Body>
                            </Card>
                        </div>

                        <h3 className="title" id="education">Education</h3>
                        <Card>
                            <Card.Header>
                                <div className="education-container">
                                    <img src={wisc} alt="wisc-icon"/>
                                    <div className="info-container">
                                        <h4>University of Wisconsin-Madison</h4>
                                        <h5>Masters, Letter and Science</h5>
                                        <p>Aug 2020 - present <br/> <b>Major in</b> Computer Science <br/> <b>Cumulative GPA:</b> 3.79</p>
                                    </div>
                                </div>
                            </Card.Header>
                            <Card.Body className="education-container">
                                <img src={wisc} alt="wisc-icon"/>
                                <div className="info-container">
                                    <h4>University of Wisconsin-Madison</h4>
                                    <h5>Bachelors, Letter and Science</h5>
                                    <p>Aug 2017 - Dec 2019 <br/> <b>Major in</b> Computer Science</p>
                                </div>
                            </Card.Body> 
                        </Card>
                        <h3 className="title" id="work">Work Experiences</h3>
                        <Card className="end-container">
                            <Card.Body className="education-container">
                                <div className="info-container">
                                        <h5>Research Assistant, Wisconsin HCI Laboratory</h5>
                                        <p>Oct 2019 – Feb 2020 <br/> Work on a software environment for robot work; improve the front-end view interfacing systems including ROS and Angular.</p>
                                </div>
                            </Card.Body> 
                        </Card>
                    </Col>
                </Row>
            </Container>
        </div>
    );
};

export default Home;