import React from "react";
import Navbar from 'react-bootstrap/Navbar';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import '../styles/Navigation.css';

const Navigation = () => {
    return (
        <Navbar className="nav-container" expand="md">
        <Container>
            <Navbar.Brand href="#home">Takashi Matsuzawa</Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse className="justify-content-end">
            <Nav>
                <Nav.Link className="nav-block" href="#about">ABOUT</Nav.Link>
                <Nav.Link className="nav-block" href="#project">PROJECT</Nav.Link>
                <Nav.Link className="nav-block" href="#education">EDUCATION</Nav.Link>
                <Nav.Link className="nav-block" href="#work">WORK</Nav.Link>
            </Nav>
            </Navbar.Collapse>
        </Container>
        </Navbar>
    );
};

export default Navigation;