import React from "react";
import person from '../assets/person.png';
import gitsm from '../assets/GitHub-Mark-32px.png';
import linkedinsm from '../assets/linkedin.png';
import linkedinsmbw from '../assets/linkedin-bw.png';
import facebooksm from  '../assets/facebook.png';
import facebooksmbw from  '../assets/facebook-bw.png';
import sayingsm from '../assets/saying.png';
import Button from 'react-bootstrap/Button';
import Popover from 'react-bootstrap/Popover';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import { GrLocation, GrLinkTop } from "react-icons/gr";
import { AiOutlineMail } from "react-icons/ai";
import { IoGlobe } from "react-icons/io5";
import '../styles/Sidebar.css';


const Sidebar = () => {
    const availability = (
        <Popover >
          <Popover.Body>
            Available for part-time work after 12/2021, and full-time after 06/2022
          </Popover.Body>
        </Popover>
    );
    const relocation = (
        <Popover >
          <Popover.Body>
            United States and Japan
          </Popover.Body>
        </Popover>
    );
    return (
        <div className="sticky">
            <img className="profile-img" src={person} alt="profile-img" />
            <div className="sidebar-section"> 
                <h3 className="sidebar-name"> Takashi Matsuzawa </h3>
                <div className="sidebar-group">
                    <img src={gitsm} alt="git-icon"/>
                    <Button variant="blanc" size="left" rel="noreferrer" target="_blank" href="https://github.com/tmatsuzawa2">tmatsuzawa2</Button>
                </div>
                <div className="sidebar-group">
                    <a href="https://www.facebook.com/takashi.matsuzawa.142/" rel="noreferrer" target="_blank">
                        <img 
                            src={facebooksmbw} 
                            alt="linkedin-icon"
                            onMouseEnter = {e => e.currentTarget.src = facebooksm}
                            onMouseLeave = {e => e.currentTarget.src = facebooksmbw}/>
                        </a>
                    <a href="http://www.linkedin.com/in/takashi-matsuzawa-033898160" rel="noreferrer" target="_blank">
                        <img 
                            src={linkedinsmbw} 
                            alt="linkedin-icon"
                            onMouseEnter = {e => e.currentTarget.src = linkedinsm}
                            onMouseLeave = {e => e.currentTarget.src = linkedinsmbw}/>
                    </a>   
                </div>
            </div>
            <div class="border-top">
                <div className="sidebar-section"> 
                    <div className="sidebar-group"> 
                        <OverlayTrigger trigger="click" placement="right" overlay={availability}>
                            <Button variant="success" size="sm">Available for Hire: 2022 Spring ~</Button>
                        </OverlayTrigger>   
                    </div>
                    <div className="sidebar-group"> 
                        <OverlayTrigger trigger="click" placement="right" overlay={relocation}>
                            <Button variant="success" size="sm">Willing to Relocate</Button> 
                        </OverlayTrigger>
                    </div>
                    <div className="sidebar-group">    
                        <GrLocation /> Madison, WI, United States
                    </div>
                    <div className="sidebar-group">    
                        <AiOutlineMail /> takashi.matsuzawa@hotmail.com
                    </div>
                    <div className="sidebar-group">    
                        <IoGlobe /> https://tmatsuzawa2.github.io
                    </div>
                    <div className="sidebar-group">
                        <Button variant="secondary" size="sm" disabled>C</Button>
                        <Button variant="secondary" size="sm" disabled>Node.js</Button>
                        <Button variant="secondary" size="sm" disabled>React</Button>
                        <Button variant="secondary" size="sm" disabled>Python</Button>
                        <Button variant="secondary" size="sm" disabled>Java</Button>
                    </div>
                </div>
            </div>
            <div class="border-top">
                <div className="sidebar-section"> 
                    <div className="sidebar-group"> 
                        <h4> Organization </h4>
                    </div>
                    <div className="sidebar-group"> 
                        <a href="https://www.sayingtheatre.com/en" rel="noreferrer" target="_blank">
                            <img className="org-logo" src={sayingsm} alt="saying-icon"/>
                        </a>
                    </div>
                </div>
            </div>
            <div class="right-down-corner">
                <a href="/#home">
                    <GrLinkTop />
                </a>
            </div>
        </div>
    );
};

export default Sidebar;